import { useCallback, useEffect, useState } from 'react'
import { useWallet } from '@binance-chain/bsc-use-wallet'
import { TreasuryAllocationTime } from 'utils'
import { getTokenAddress, getTreasury } from 'utils/addressHelpers'
import multicall from 'utils/multicall'
import { QuoteToken } from 'config/constants/types'
import treasury from 'config/abi/treasury.json'
import BigNumber from 'bignumber.js'
import { useBoardroom, useOracle } from './useContract'

const useExitBoardroom = () => {
  const { account } = useWallet()
  const contract = useBoardroom()

  const handleExit = useCallback(async () => {
    const txHash = await contract.methods.exit().send({ from: account })
    return txHash
  }, [account, contract])

  return { onRedeem: handleExit }
}

const oneUnit = new BigNumber(10).pow(18)

export const useCashPriceInEstimatedTWAP = () => {
  const [stat, setStat] = useState(new BigNumber(10).div(oneUnit))
  const contract = useOracle()
  const sdo = getTokenAddress(QuoteToken.SDO)

  const fetchCashPrice = useCallback(async () => {
    const cashPrice = await contract.methods.twap(sdo, oneUnit).call()
    console.info(cashPrice.toString())
    setStat(new BigNumber(cashPrice).div(oneUnit))
  }, [contract, sdo])

  useEffect(() => {
    fetchCashPrice().catch((err) => console.error(`Failed to fetch price: ${err.stack}`))
    const refreshInterval = setInterval(fetchCashPrice, 10000)
    return () => clearInterval(refreshInterval)
  }, [fetchCashPrice, setStat])

  return stat
}

