export const useBondPriceInEstimatedTWAP = () => {
  const [stat, setStat] = useState(new BigNumber(10).div(oneUnit))
  const contract = useOracle()
  const sdo = getTokenAddress(QuoteToken.SBO)

  const fetchCashPrice = useCallback(async () => {
    const cashPrice = await contract.methods.twap(sdo, oneUnit).call()
    setStat(new BigNumber(cashPrice).div(oneUnit))
  }, [contract, sdo])

  useEffect(() => {
    fetchCashPrice().catch((err) => console.error(`Failed to fetch price: ${err.stack}`))
    const refreshInterval = setInterval(fetchCashPrice, 10000)
    return () => clearInterval(refreshInterval)
  }, [fetchCashPrice, setStat])

  return stat
}

export const useTreasuryAllocationTimes = () => {
  const [time, setTime] = useState<TreasuryAllocationTime>({
    prevAllocation: new Date(),
    nextAllocation: new Date(),
    nextEpochLength: 0,
  })
  const contract = getTreasury()

  useEffect(() => {
    const fetchAllocations = async () => {
      const calls = [
        {
          address: contract,
          name: 'nextEpochPoint',
          params: [],
        },
        {
          address: contract,
          name: 'lastEpochTime',
          params: [],
        },
        {
          address: contract,
          name: 'nextEpochLength',
          params: [],
        },
      ]

      const [nextEpochPoint, lastEpochTimestamp, nextEpochLength] = await multicall((treasury as unknown) as any, calls)
      setTime({
        nextAllocation: new Date(new BigNumber(nextEpochPoint).multipliedBy(1000).toNumber()),
        prevAllocation: new Date(new BigNumber(lastEpochTimestamp).multipliedBy(1000).toNumber()),
        nextEpochLength: new BigNumber(nextEpochLength).toNumber(),
      })
    }

    if (contract) fetchAllocations()
  }, [contract])
  return time
}

export default useExitBoardroom
